import React, { useState, useEffect } from 'react';
import { postNewData } from '../request/request';
import Loading from '../components/Loading';

const NewData = (props) => {
	const [ inputs, setInputs ] = useState({});
	const [ isLoading, setLoading ] = useState(false);
	const [ fields, setFields ] = useState([]);
	const [ isProcessing, setProcessing ] = useState(false);
	const [ err, setErr ] = useState('');

	useEffect(
		() => {
			setLoading(true);
			setFields(props.fields);
			setLoading(false);
		},
		[ props.fields ]
	);

	const handleChange = (e) => {
		const { name, value } = e.target;
		setInputs({ ...inputs, [name]: value });
	};

	function handleSubmit(e) {
		setProcessing(true);
		e.preventDefault();
		let type = props.type;
		if (!type) return;
		if (!isProcessing) {
			let clearInputs = {};

			postNewData(inputs, type).then((res) => {
				if (res) {
					if (props.clear) {
						setInputs(clearInputs);
						setProcessing(false);
					}
					if (props.onSuccess) {
						props.onSuccess(res);
					}
				} else {
					setErr('Something wrong with your input');
					if (props.onError) {
						props.onError();
					}
					setProcessing(false);
				}
			});
		}
	}

	if (isLoading) return <Loading />;

	return (
		<div className="ui container mb-40">
			<h3 className="ui dividing header">{props.title || 'Add a new home content'}</h3>
			<form className="ui form error" onSubmit={(e) => handleSubmit(e)}>
				{fields &&
					fields.map((field) => (
						<div key={field.name || field} className="field">
							<label className="capitalize">{field.name || field}</label>
							{field.type ? (
								<input
									type={field.type}
									value={inputs[field.name] || ''}
									name={field.name}
									rows="3"
									required={field.required}
									onChange={(e) => handleChange(e)}
								/>
							) : (
								<textarea
									value={inputs[field] || ''}
									name={field}
									rows="3"
									onChange={(e) => handleChange(e)}
								/>
							)}
						</div>
					))}
				{err && <p className="color-red">{err}</p>}

				<button className={`ui ${isProcessing ? 'loading' : ''} button`} type="submit">
					Submit
				</button>
			</form>
		</div>
	);
};

export default NewData;
