import React, { useEffect, useState } from "react";
import { getFollowedSeries } from "../request/request";
import GridWrap from "../components/GridWrap";
import Loading from "../components/Loading";
import Tab from "./Tab";
import { Link } from "react-router-dom/cjs/react-router-dom.min";

const Followed = () => {
	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(true);

	useEffect(() => {
		setLoading(true);
		document.title = "aniba - Your followed animes";
		let storedData = sessionStorage.getItem('followedData')
		if (storedData) storedData = JSON.parse(storedData)
		if (Array.isArray(storedData) && storedData.length > 0) {
			setData(storedData)
			setLoading(false)
		}

		getFollowedSeries()
			.then(res => {
				if (res && res.data) {
					setData(res.data);
					sessionStorage.setItem('followedData', JSON.stringify(res.data))
				}
				setLoading(false);

			})
			.catch(err => {
				console.log(err);
				setLoading(false);
			});
	}, []);

	return (
		<div>
			<div className="page-fade-in">
				<div className="pt-sm-20">
					<div className="ui container pb-10">
						<h3>Your Animes</h3>
					</div>

					{!isLoading && data && data.length === 0 && (
						<div className="centered">
							{localStorage.getItem('userId') ?
								<p className="text-center pt-10">
									You have nothing. Open a series and click follow!
							</p>
								:
								<Link to="/auth/register" className="my-1 fluid ui blue button">Authorize</Link>
							}
						</div>
					)}
					{isLoading ? (
						<Loading />
					) : (
							<div className="mb-40">
								<GridWrap data={data} />
							</div>
						)}
				</div>
			</div>
			<Tab active="Followed"></Tab>
		</div>
	);
};

export default Followed;
