import axios from 'axios';

export const url = 'https://anibaniba.herokuapp.com';

export async function getHome(key) {
	try {
		let { data } = await axios.get(`${url}/api/home?json=true&${key}`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function getFields() {
	try {
		let { data } = await axios.get(`${url}/api/stats/home/new`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function getFollowedSeries() {
	try {
		let userId = localStorage.getItem('userId')
		let { data } = await axios.get(`${url}/api/user/${userId}/followed`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function postFollowedSeries(seriesId) {
	try {
		let userId = localStorage.getItem('userId')
		if (!userId.includes('__')) return
		let { data } = await axios.post(`${url}/api/user/${userId}/followed/${seriesId}`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function postNewData(body, type) {
	try {
		let postUrl = `${url}/api/stats/home/new`;
		if (type === 'login' || type === 'register') postUrl = `${url}/api/auth/${type}`;
		let { data } = await axios.post(postUrl, body);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function getDetail(id) {
	try {
		let { data } = await axios.get(`${url}/api/episodes/${id}?json=true`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getSearch(query) {
	try {
		let { data } = await axios.get(`${url}/api/search?json=true&q=${query}`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getSeries(id) {
	try {
		let { data } = await axios.get(`${url}/api/series/${id}?json=true`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getStats(key) {
	try {
		let { data } = await axios.get(`${url}/api/stats/${key}`);
		return data;
	} catch (err) {
		console.log(err);
	}
}

export async function deleteData(type, key) {
	try {
		let { data } = await axios.delete(`${url}/api/stats/${type}?${key}`);
		return data.data;
	} catch (err) {
		console.log(err);
	}
}

export async function getComments(parentId) {
	try {
		let { data } = await axios.get(`${url}/api/comments/${parentId}`);
		return data.comments;
	} catch (err) {
		console.log(err);
	}
}

export async function postComment(parentId, text) {
	let authorId = localStorage.getItem('userId');

	if (!authorId) authorId = localStorage.setItem('userId', Date.now());
	let body = {
		authorId,
		parentId,
		text
	};
	try {
		let { data } = await axios.post(`${url}/api/comments/${parentId}`, body);
		return data.comment;
	} catch (err) {
		console.log(err);
	}
}
