import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Link } from 'react-router-dom/cjs/react-router-dom.min';

const AppBar = (props) => {
	const [input, setInput] = useState('');

	let history = useHistory();

	const handleSearch = (e) => {
		e.preventDefault();
		history.push(`/search/${input}`);
	};

	return (
		<div id="app-bar">
			<div className="ui container small secondary menu">
				{props.showHome &&
					<span className="item active cursor-pointer" onClick={() => history.replace('/')}>
						H
					</span>
				}
				<div className="item" id="search-bar">
					<form action="/search/" method="GET" className="ui icon input" onSubmit={(e) => handleSearch(e)}>
						<input
							autoFocus={props.autoFocus}
							type="text"
							name="q"
							placeholder="Search anime titles..."
							value={input}
							onChange={(e) => setInput(e.target.value)}
						/>
						<i className="search link icon" />
					</form>
				</div>
				{props.auth &&
					<Link className="item active cursor-pointer" to="/auth/login">
						AU
					</Link>
				}
			</div>
		</div>
	);
};

export default AppBar;
