import React, { useEffect, useState } from 'react';
import Form from './Form';
import { getFields } from '../request/request';

const NewData = () => {
	const [ fields, setFields ] = useState({
		title: '',
		inputs: []
	});

	useEffect(() => {
		getFields().then((res) => {
			console.log(res);
			setFields({ title: 'Add a new content to Home section', inputs: res.data });
		});
	}, []);

	return <Form type="home" title={fields.title} fields={fields.inputs} clear={true} />;
};

export default NewData;
