import React, { useEffect, useState } from 'react';
import Form from './Form';
import { useHistory, useParams } from 'react-router-dom';

const login = {
	title: 'Authorize Yourself',
	inputs: [
		{ name: 'username', type: 'text', required: true },
		{ name: 'password', type: 'password', required: true }
	]
};
const register = {
	title: 'Register',
	inputs: [
		{ name: 'username', type: 'text', required: true },
		{ name: 'password', type: 'password', required: true }
	]
};

const Auth = (props) => {
	const [fields, setFields] = useState({
		title: '',
		inputs: []
	});
	let history = useHistory();
	let { type } = useParams();

	useEffect(
		() => {
			let authorized = props.user.authenticated;
			if (authorized) {
				history.replace('/followed');
			}
			let fields = [];
			if (type === 'login') {
				fields = login;
			} else if (type === 'register') {
				fields = register;
			}
			setFields(fields);
		},
		[type, history, props.user]
	);

	const onSuccess = (res) => {
		localStorage.setItem('userId', res.data._id + '__' + res.data.username);
		history.replace('/');
		props.setUser({
			id: res.data._id,
			name: res.data.username,
			authenticated: true
		});
	};

	if (!fields || fields.inputs.length < 1) {
		return <div />;
	}

	return (
		<div id="auth" className="page-fade-in">
			<span className="fixed-right">
				{type === 'login' ?
					<span onClick={() => history.replace('register')} className="cursor-pointer f-bold">
						Register
					</span>
					:
					<span onClick={() => history.replace('login')} className="cursor-pointer f-bold">
						Login
					</span>
				}
				<span>{'  |  '}</span>
				<span onClick={() => history.replace('/')} className=" cursor-pointer f-bold">
					Skip
				</span>
			</span>
			<Form
				type={type}
				title={fields.title}
				fields={fields.inputs}
				onSuccess={onSuccess}
				className=" cursor-pointer f-bold"
			/>
		</div>
	);
};

export default Auth;
