import React, { useState, useEffect } from "react";
import GridList from "../components/GridList";
import { getHome } from "../request/request.js";
import Loading from "../components/Loading";
import Tab from "./Tab";

const Home = () => {
	const [homeData, setData] = useState([])
	const [isLoading, setLoading] = useState(false)

	useEffect(() => {
		setLoading(true)
		let allData = []
		let silent = false

		let storedHomeData = sessionStorage.getItem('homeData')

		if (storedHomeData) {
			storedHomeData = JSON.parse(storedHomeData)
			setData(storedHomeData)
			silent = true
			setLoading(false)
		}

		const fecthNewReleases = async () => {
			let { data, total } = await getHome("type=new_releases");
			if (!silent) {
				setData(data)
			}
			allData = [...data]
			setLoading(false)
			fetchFeatured(total)
		};

		const fetchFeatured = async (featuredTotal) => {
			for (let i = 0; i < featuredTotal; i++) {
				if (!silent) {
					setLoading(true)
				}
				let { data } = await getHome(`type=browse&limit=1&skip=${i}`);
				if (!silent) {
					setData(homeData => [...homeData, ...data])
				}
				allData = [...allData, ...data]
				setLoading(false)
			}
			setData(allData)
			sessionStorage.setItem('homeData', JSON.stringify(allData))

		}
		fecthNewReleases()
	}, [])


	return (
		<div>
			<div className="mb-40 pt-sm-20 mb-sm-70 page-fade-in">
				<div className="grid-scroll-blocker" />
				{homeData.map(item => (
					<GridList
						data={item.content}
						title={item.title}
						key={item._id}
					/>
				))}

				{isLoading && <Loading />}
			</div>
			<Tab active="Home"></Tab>
		</div>
	);

}

export default Home
