import React, { useState, useEffect, useRef } from 'react';
import { getComments, postComment } from '../request/request';
import CommentItem from '../components/CommentItem';
import Loading from '../components/Loading';

const Comments = (props) => {
	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(false);
	const [commentInput, setComment] = useState('');
	const [isPostLoading, setPostLoading] = useState(false);
	const [mobileFormStatus, setFormStatus] = useState('hide');
	const replyBtn = useRef()

	useEffect(
		() => {
			function fetchComments() {
				getComments(props.parentId)
					.then((res) => {
						setData(res);
						setLoading(false);
					})
					.catch((err) => {
						setLoading(false);
						console.log(err);
					});
			}
			setLoading(true);
			fetchComments();
			const interval = setInterval(() => fetchComments(), 45000);
			return () => clearInterval(interval);
		},
		[props.parentId]
	);

	const handleNewComment = (e) => {
		if (!isPostLoading) {
			setPostLoading(true);
			e.preventDefault();
			postComment(props.parentId, commentInput).then((res) => {
				if (res) {
					setData([...data, res]);
					setComment('');
					setPostLoading(false);
					if (mobileFormStatus !== 'hide') {
						replyBtn.current.scrollIntoView({ behavior: 'smooth' })
						showForm(false)
					}
				} else {
					setPostLoading(false);
				}
			});
		}
	};

	const showForm = (bool) => {
		if (bool) {
			setFormStatus('opacity-0');
			setTimeout(() => setFormStatus('opacity-1'), 0);
		} else {
			setFormStatus('opacity-0');
			setComment('')
			setTimeout(() => setFormStatus('hide'), 300);
		}
	};

	if (isLoading) {
		return <Loading />;
	}

	return (
		<div className="mb-40 pt-10">
			<h3 className="ui dividing header ellipsis pb-10">{props.title}</h3>
			{!data || data.length === 0 ? (
				<p className="pb-20">There's no comment</p>
			) : (
					<div className="ui comments">{data.map((item) => <CommentItem data={item} key={item._id} />)}</div>
				)}

			<form className="ui reply form hide-xs" id="comment-form" onSubmit={(e) => handleNewComment(e)}>
				<div className="field">
					<textarea
						id="comment-box"
						rows="3	"
						onChange={(e) => setComment(e.target.value)}
						value={commentInput}
					/>
				</div>
				<button className={`ui blue labeled ${isPostLoading ? 'loading' : ''} submit icon button`}>
					<i className="icon edit" /> Add Reply
				</button>
			</form>

			<div className="my-1 hide-lg" ref={replyBtn} >
				<button className="fluid ui blue button" onClick={() => showForm(true)}>
					Add Reply
				</button>
			</div>

			{mobileFormStatus !== 'hide' && (
				<div className={`fixed-form ${mobileFormStatus}`}>
					<div className="dummy-space" onClick={() => showForm(false)} />
					<form className="ui reply form" onSubmit={(e) => handleNewComment(e)}>
						<div className="field">
							<textarea
								autoFocus={true}
								rows="3"
								onChange={(e) => setComment(e.target.value)}
								value={commentInput}
							/>
						</div>
						<button className={`ui blue ${isPostLoading ? 'loading' : ''} button`}>Submit</button>
						<button className={`ui button`} type="button" onClick={() => showForm(false)}>
							Cancel
						</button>
					</form>
				</div>
			)}
		</div>
	);
};

export default Comments;
