import React, { useState, useEffect } from "react";
import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./containers/Home";
import Detail from "./containers/Detail";
import Series from "./containers/Series";
import Search from "./containers/Search";
import Stats from "./containers/Stats";
import NewData from "./containers/NewData";
import Auth from "./containers/Auth";
import Followed from "./containers/Followed";
import AppBar from "./components/AppBar";

const App = () => {
	const [user, setUser] = useState({
		id: "",
		name: "",
		authenticated: false
	});

	useEffect(() => {
		let auth = localStorage.getItem("userId");
		if (auth) {
			auth = auth.split("__");
			if (auth[0] && auth[1]) {
				setUser({
					id: auth[0],
					name: auth[1],
					authenticated: true
				});
			}
		}
	}, []);

	useEffect(() => {
		let OneSignal = window.OneSignal || [];
		if (user.authenticated) {
			OneSignal.push(function () {
				OneSignal.setExternalUserId(user.id);
				OneSignal.showSlidedownPrompt();
			});
		}
	}, [user]);

	return (
		<Router>
			<div>
				<div className="hide-xs">
					<AppBar showHome={true} auth={true}></AppBar>
					<div className="spacer"></div>
				</div>
				<Switch>
					<Route path="/auth/:type">
						<Auth user={user} setUser={setUser} />
					</Route>
					<Route path="/stats/home/new">
						<NewData />
					</Route>
					<Route path="/stats/:type">
						<Stats />
					</Route>
					<Route path="/series/:seriesId">
						<Series />
					</Route>

					<Route path="/search/:searchKey">
						<Search />
					</Route>
					<Route path="/search">
						<Search />
					</Route>
					<Route path="/followed">
						<Followed />
					</Route>

					<Route path="/:episodeId">
						<Detail />
					</Route>
					<Route path="/">
						<Home />
					</Route>
				</Switch>
			</div>
		</Router>
	);
};

export default App;
