import React from "react";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

const Tab = ({ active }) => {
	let history = useHistory()
	const tabs = [
		{ label: "Home", icon: "Ho", href: "/" },
		{ label: "Search", icon: "Se", href: "/search" },
		{ label: "Followed", icon: "Fw", href: "/followed" }
	];

	const handleClick = (href) => {
		history.replace(href)
	}

	return (
		<div className="hide-lg">
			<div className="tab-container">
				{tabs.map(item => (
					<TabItem data={item}
						handleClick={handleClick}
						key={item.icon}
						active={active}></TabItem>
				))}
			</div>
		</div>
	);
};

const TabItem = ({ data, active, handleClick }) => {
	return (
		<div
			onClick={() => handleClick(data.href)}
			className={`tab-item ${active === data.label ? "tab-active" : ""}`}
		>
			<p className="tab-icon">{data.icon}</p>
			<p className="tab-label">{data.label}</p>
		</div >
	);
};

export default Tab;
