import React, { useState, useEffect } from 'react';
import ItemList from '../components/ItemLists';
import { getSeries, postFollowedSeries } from '../request/request';
import { useParams, Redirect } from 'react-router-dom';
import Loading from '../components/Loading';
import AppBar from '../components/AppBar';

const Series = () => {
	const [data, setData] = useState({});
	const [isLoading, setLoading] = useState(true);
	const [isFollowed, setFollowed] = useState(false);
	const { seriesId } = useParams();
	useEffect(
		() => {
			setLoading(true);
			getSeries(seriesId)
				.then((res) => {
					setData(res);
					if (res.followedBy && res.followedBy.length > 0) {
						let userId = localStorage.getItem('userId')
						if(userId.includes('__')){
							userId = userId.split('__')[0]
							if (res.followedBy.includes(userId)) {
								setFollowed(true)
							}
						}
					}
					document.title = `Watch ${res.title} for free without ads`;
					setLoading(false);
				})
				.catch((err) => {
					console.log(err);
				});
		},
		[seriesId]
	);

	const handleFollow = () => {
		postFollowedSeries(seriesId).then(res => {
			if (res) {
				if (res.note === 'FOLLOWED') {
					setFollowed(true)
				} else if (res.note === 'UNFOLLOWED') {
					setFollowed(false)
				}
			}
		})
	}

	if (!isLoading && !data.title) {
		return <Redirect to="/" />;
	}

	if (isLoading) {
		return <Loading />;
	}
	return (
		<div className="page-fade-in">
			<div className="hide-lg">
				<AppBar showHome={true}></AppBar>
				<div className="spacer"></div>
			</div>

			<div className="ui container mb-40">
				<div className="ui grid">
					<div className="large screen only four wide column">
						<img className="series-img" src={data.img} alt="cover" />
						<div className="hide-sm">
							<button className={`fluid ui ${isFollowed ? 'red' : 'blue'} button`} onClick={handleFollow}>
								{isFollowed ? 'Unfollow' : 'Follow'}
							</button>
						</div>
					</div>
					<div className="large screen only twelve wide column">
						<h1>{data.title}</h1>
						<p>{data.status}</p>
						<p>{data.desc}</p>
					</div>
					<div className="mobile only sixteen wide column">
						<div className="series-img-m ui placeholder">
							<div className="ui image">
								<img width="100%" height="200" src={data.img} alt="cover" />
							</div>
						</div>
						<h1>{data.title}</h1>
						<p>{data.status}</p>
						<p>{data.desc}</p>
						<div className="mt-15 text-center px-20">
							<button className={`fluid ui ${isFollowed ? 'red' : 'blue'} button`} onClick={handleFollow}>
								{isFollowed ? 'Unfollow' : 'Follow'}
							</button>
						</div>
					</div>
				</div>

				{data && data.episodes && <ItemList data={data.episodes} />}

			</div>
		</div>
	);
};

export default Series;
